.home{
    background-color: #ff1493;
    text-align: center;
    height: 100vh;

}
.home-card{
    text-align: center;
    width: 50%;
    padding:10px;
    margin: auto;
    position: relative;
    top:0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #ff1493;
}

.animation{
    margin: auto;
    text-align: center;
    max-width: 80%
}

.animation img{
    max-width: 100%
}


.home button{
    padding: 5px;
    border-radius: 5px;
    margin:2px;
}

.home button:active {
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
    transform: translateY(4px);
  }


  .footer {
    background-color: #ff1493;
    white-space: nowrap;
    overflow: hidden;
  }
  
  .footer span {
    display: inline-block;
    padding-left: 100%;
    animation: footers 5s linear infinite;
  }
  

  @keyframes footers {
    0% {
      transform: translate(0, 0);
    }
    100% {
      transform: translate(-100%, 0);
    }
  }



@media screen and (min-width: 700px){
    .animation img{
        max-width: 600px
    }
}
