.game{
    background-color: #ff1493;
}
.tambola{
    display: grid;
    text-align: center;
    margin-top: 200px;
    background-color: #ff1493;

}

.chit{
    grid-column: 1/1;
    box-sizing: border-box;
    text-align: center;
    width: 100%;
}

.chit div{
    margin-left: auto;
  margin-right: auto;
  display: table;
    width: 360px;
    height: 180px;
    margin-bottom : 25px
}

.chit_numbers{
    background-color: white;
    width: 60px;
    height: 60px;
    border:1px solid black;
    font-size: 1.5rem;
}

.number-generator{
    position: fixed;
    top: 10;
    left: 45%;
    z-index: 2;
}

.number-generator-button{
    font-size: 2rem;
    background-color: #fdc858;
    color: #ff1493;
    font-weight: 800;
    width: 100px;
    height: 100px;
    border-radius: 50%;
    /* transition: transform 5s ease-in; */
    transform: 3d;
    position: fixed;
    -webkit-transform: translate3d(0,0,0);
    -webkit-transition: all .25s ease;
    -moz-transition: all .25s ease;
    -ms-transition: all .25s ease;
    -o-transition: all .25s ease;
    transition: all .25s ease;
}


.Rules button{
    padding: 5px;
    border-radius: 5px;
    margin:2px;
    background-color: #4CAF50;
    color: white;
}

.Rules button:active {
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
    transform: translateY(4px);
  }


@media (max-width: 360px) {
