.lobby{
    display: grid;
    background-color: #ff1493;

}

.players{
    grid-column: 1/1;
    display: flex;
    flex-wrap: wrap;
}


.chat{
    grid-column: 1/1;
    text-align: center;
}

.chat{
    margin: 4px;
    height: 500px;
    background-color: #ff1493;
 /*    grid-column: 3 / 4 ;
    grid-row: 1; */
    overflow: scroll;
    flex: 1;
  }

  .chat-container{
    border: 2px solid #ffb6c1;
    background-color: #ff69b4;
    border-radius: 5px;
    padding: 10px;
    margin: 10px 4px;
  }

.lobby button{
    padding: 5px;
    border-radius: 5px;
    margin:2px;
    cursor: pointer;
    transition-duration: 0.4s;
}

.lobby button:active{
    box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
    transform: translateY(4px);
}



.ready_Button{
    background-color: #4CAF50; /* Green */
    color: white;
}


.leave_Button:hover{
    background-color: red; /* Green */
    color: white;
}






.players_images{
    max-width: 160px;
    box-sizing: border-box;
}

.players_images img{
    width: 60%;
    border-radius: 50%;
}

.players_images {
    transition: all ease-in 2s;
}

.players_images:hover{ 
    animation: rotate 1s linear; 
} 

@keyframes rotate{ 
    0%{ 
        transform: rotate(0deg); 
    } 
    50%{ 
        transform: rotate(180deg); 
    } 
    100%{ 
        transform: rotate(360deg); 
    } 
}